import React from "react";
import { Divider, Flex, Text } from "@chakra-ui/react";

export const Underline = ({ children, border, mt, mb, ...props }) => (
  <Flex direction={"column"}>
    <Text {...props} mb={1} mt={mt}>
      {children}
    </Text>
    <Divider mb={mb} borderWidth={border} opacity={1} />
  </Flex>
);
