import detectEthereumProvider from "@metamask/detect-provider";
import React, { useState, useEffect } from "react";
import { ethers } from "ethers";
import { Box, Button, Center, Text } from "@chakra-ui/react";

const background = (image) => ({ backgroundImage: `url(${image})` });

const addToken = (address, symbol, decimals, image) => async () => {
  const provider = await detectEthereumProvider();

  const tokenInfo = {
    method: "metamask_watchAsset",
    params: {
      type: "ERC20",
      options: {
        address,
        symbol,
        decimals,
        image: window.location.origin + image,
      },
    },
    id: Math.round(Math.random() * 100000),
  };

  provider.sendAsync(tokenInfo, (err, added) => {
    console.log({ err, added });
  });
};

export default function TokenCard(props) {
  const [supply, setSupply] = useState(0);
  const [name, setName] = useState("");
  const [symbol, setSymbol] = useState("");

  async function requestAccount() {
    await window.ethereum.request({ method: "eth_requestAccounts" });
  }

  const getName = async () => {
    if (typeof window.ethereum === "undefined") {
      return;
    }

    const provider = new ethers.providers.Web3Provider(window.ethereum);
    const contract = new ethers.Contract(props.tokenAddress, props.tokenAbi, provider);
    setName(await contract.name());
  };

  const getSymbol = async () => {
    if (typeof window.ethereum === "undefined") {
      return;
    }

    const provider = new ethers.providers.Web3Provider(window.ethereum);
    const contract = new ethers.Contract(props.tokenAddress, props.tokenAbi, provider);
    setSymbol(await contract.symbol());
  };

  const getSupply = async () => {
    console.log({ props });

    if (typeof window.ethereum === "undefined") {
      return;
    }

    const provider = new ethers.providers.Web3Provider(window.ethereum);
    const contract = new ethers.Contract(props.tokenAddress, props.tokenAbi, provider);
    setSupply(await contract.totalSupply());
  };

  useEffect(() => {
    getSupply();
    getSymbol();
    getName();
  }, []);

  return (
    <Box bgImage={props.cardImage} bgSize={"cover"} w="354px" h="650px" color={"white"} p={10}>
      <Center h={200}>
        <img src={props.tokenImage} alt="" />
      </Center>
      <Text fontWeight={800} fontSize={20} textAlign={"center"}>
        {name}
      </Text>
      <Text textAlign={"justify"} mt={5} fontSize={13}>
        {props.children}
      </Text>
      <Text textAlign={"center"} mt={5} fontSize={13} fontWeight={200}>
        Total Supply
      </Text>
      <Text textAlign={"center"} fontSize={18} fontWeight={800}>
        {new Intl.NumberFormat("de-DE").format(Math.ceil(supply / Math.pow(10, 18)))}
      </Text>
      <Button
        mt={5}
        isFullWidth
        rounded={25}
        variant={"outline"}
        onClick={addToken(props.tokenAddress, symbol, 18, props.tokenIcon)}
      >
        Add Token
      </Button>
    </Box>
  );
}
