import React, { useRef, useCallback, useState } from "react";
import { Carousel } from "react-responsive-carousel";

import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader

import wvideo1 from "../assets/display/1.mp4";
import wvideo2 from "../assets/display/2.mp4";
import wvideo3 from "../assets/display/3.mp4";
import wvideo4 from "../assets/display/4.mp4";

import mvideo1 from "../assets/mobile/1.mp4";
import mvideo2 from "../assets/mobile/2.mp4";
import mvideo3 from "../assets/mobile/3.mp4";
import mvideo4 from "../assets/mobile/4.mp4";

import useWindowDimensions from "../hooks/useWindowDimensions";

const Video = ({ src }) => {
  const videoRef = useRef(null);
  const [playing, setPlaying] = useState(false);

  const handlePlayPause = useCallback(() => {
    videoRef.current.play();
    videoRef.current.onpause = () => {
      // console.log({ carouselRef });
      // carouselRef.current.increment();
      setPlaying(false);
    };
    setPlaying(true);
  }, [videoRef]);

  return (
    <div className="video-container">
      <video ref={videoRef}>
        <source src={src} type="video/mp4" />
      </video>
      <div className="message">
        {!playing && <div onClick={handlePlayPause} className="play" />}
      </div>
    </div>
  );
};

export default function VideoCarousel(props) {
  const { widescreen } = useWindowDimensions();

  const carouselRef = useRef(null);

  const videos = [
    { wide: wvideo1, mobile: mvideo1 },
    { wide: wvideo2, mobile: mvideo2 },
    { wide: wvideo3, mobile: mvideo3 },
    { wide: wvideo4, mobile: mvideo4 },
  ];

  return (
    <Carousel
      autoPlay={false}
      showThumbs={false}
      showStatus={false}
      showArrows={false}
      useKeyboardArrows
      transitionTime={0}
      dynamicHeight
      ref={carouselRef}
    >
      {videos.map((v, i) => (
        <Video key={i} src={widescreen ? v.wide : v.mobile} carouselRef={carouselRef} />
      ))}
    </Carousel>
  );
}
