import { Box, Flex, Spacer, Text } from "@chakra-ui/react";
import React from "react";

const NavItem = ({ children, href }) => (
  <a href={href}>
    <Text ml={5} fontWeight={800} fontSize={15}>
      {children}
    </Text>
  </a>
);

const NavTitle = ({ children, href }) => (
  <a href={href}>
    <Text as="button" fontWeight={800} fontSize={25}>
      {children}
    </Text>
  </a>
);

export default function Navigation(props) {
  return (
    <Box
      position="sticky"
      color="white"
      zIndex={1000}
      left={0}
      top={0}
      width="100%"
      pl="5vw"
      pr="5vw"
      pt={5}
      pb={5}
    >
      <Flex alignItems={"center"}>
        <NavTitle href="#home">{props.title}</NavTitle>
        <Spacer />
        {props.items.map((i) => (
          <NavItem href={"#" + i} key={i}>
            {i.toUpperCase()}
          </NavItem>
        ))}
      </Flex>
    </Box>
  );
}
