import { Box, Center, Flex, Text } from "@chakra-ui/react";
import React, { Component } from "react";

const NumberBoxDivider = () => (
  <Text fontSize={40} fontWeight={800} color={"white"}>
    :
  </Text>
);

const NumberBox = ({ number }) => (
  <>
    {[...number.toString()].map((digit, k) => (
      <Flex key={k} m={2} borderWidth={1} rounded={15} h={20}>
        <Center w="70px">
          <Text fontSize={40} fontWeight={600} color={"white"}>
            {digit}
          </Text>
        </Center>
      </Flex>
    ))}
  </>
);

export default class Countdown extends Component {
  constructor(props) {
    super(props);
    this.state = { days: 0, hours: 0, minutes: 0, seconds: 0, time: 0 };
  }
  componentWillMount() {
    this.getTimeUntil(this.props.deadline);
  }
  componentDidMount() {
    setInterval(() => this.getTimeUntil(this.props.deadline), 1000);
  }
  leading0(num) {
    return num < 10 ? "0" + num : num;
  }
  getTimeUntil(deadline) {
    if (typeof deadline === "string") {
      deadline = Date.parse(deadline);
    }
    const time = deadline - Date.parse(new Date());
    if (time < 0) {
      this.setState({ days: 0, hours: 0, minutes: 0, seconds: 0, time: 0 });
    } else {
      const seconds = Math.floor((time / 1000) % 60);
      const minutes = Math.floor((time / 1000 / 60) % 60);
      const hours = Math.floor((time / (1000 * 60 * 60)) % 24);
      const days = Math.floor(time / (1000 * 60 * 60 * 24));

      this.setState({ days, hours, minutes, seconds, time });
    }
  }
  render() {
    return (
      <Flex alignItems={"center"}>
        <NumberBox number={this.leading0(this.state.days)} />
        <NumberBoxDivider />
        <NumberBox number={this.leading0(this.state.hours)} />
        <NumberBoxDivider />
        <NumberBox number={this.leading0(this.state.minutes)} />
        <NumberBoxDivider />
        <NumberBox number={this.leading0(this.state.seconds)} />
      </Flex>
    );
  }
}
