import React from "react";

import VideoCarousel from "../sections/Carousel";
import TokenCard from "../components/TokenCard";

import energyImage from "../assets/energy.png";
import matterImage from "../assets/matter.png";
import antimatterImage from "../assets/antimatter.png";
import energyIcon from "../assets/energy.jpg";
import matterIcon from "../assets/matter.jpg";
import antimatterIcon from "../assets/antimatter.jpg";
import energyCardImage from "../assets/energy_card.png";
import matterCardImage from "../assets/matter_card.png";
import antimatterCardImage from "../assets/antimatter_card.png";
import Navigation from "../components/Navigation";
import contracts from "../contracts";

import Matter from "../artifacts/contracts/Matter.sol/Matter.json";
import AntiMatter from "../artifacts/contracts/AntiMatter.sol/AntiMatter.json";
import Energy from "../artifacts/contracts/Energy.sol/Energy.json";
import LaunchTime from "../sections/LaunchTime";
import { Box, Spacer } from "@chakra-ui/react";
import { Section } from "../components/Section";
import { Tokens } from "../sections/Tokens";

import { About } from "../sections/About";
import { History } from "../sections/History";

const MatterCard = () => (
  <TokenCard
    tokenAddress={contracts.matter}
    tokenAbi={Matter.abi}
    tokenImage={matterImage}
    tokenIcon={matterIcon}
    cardImage={matterCardImage}
  />
);

const AntimatterCard = () => (
  <TokenCard
    tokenAddress={contracts.antimatter}
    tokenAbi={AntiMatter.abi}
    tokenImage={antimatterImage}
    tokenIcon={antimatterIcon}
    cardImage={antimatterCardImage}
  />
);

const EnergyCard = () => (
  <TokenCard
    tokenAddress={contracts.energy}
    tokenAbi={Energy.abi}
    tokenImage={energyImage}
    tokenIcon={energyIcon}
    cardImage={energyCardImage}
  />
);

export default function AppBar() {
  return (
    <div id="home">
      {/* NAVIGATION */}
      <Navigation title={"METAVERSE"} items={["history", "tokens", "launch"]} />

      {/* CAROUSEL */}
      <Box position={"absolute"} top={0}>
        <VideoCarousel />
      </Box>
      <Section></Section>

      {/* HISTORY */}
      <Section id="history">
        <History />
      </Section>

      {/* TOKENS */}
      <Section id="tokens">
        <Tokens />
      </Section>

      {/* LAUNCH */}
      <Section id={"launch"}>
        <LaunchTime />
      </Section>

      {/* ABOUT */}
      <About />
    </div>
  );
}
